
#animation #cloud {
  height: 12vh;
  position: absolute;
  z-index: -1;
}
#animation #cloud .st0 {
  opacity: 0.9;
  fill: #fff;
  stroke: #000;
  stroke-width: 5;
  stroke-linecap: round;
  stroke-linejoin: round;
  enable-background: new;
}
#animation #cloud .st1 {
  opacity: 0.75;
  enable-background: new;
}
#animation #mountains {
  width: 50%;
  position: absolute;
  z-index: -2;
  transform: translate(-50%, 10%);
  left: 50%;
  bottom: 10vh;
}
#animation #mountains .cls-1 {
  fill: #d3b497;
}
#animation #mountains .cls-2 {
  fill: #a08670;
}
#animation #mountains .cls-3 {
  fill: #b1ddbc;
}
#animation #mountains .cls-4 {
  fill: #c3e0ff;
}
#animation #mountains .cls-5 {
  fill: #f0f8ff;
}
#animation #mountains .cls-6,
#animation #mountains .cls-7 {
  fill: #fff;
}
#animation #mountains .cls-7 {
  stroke: #000;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 0;
}
#animation #mountains .cls-8 {
  fill: #192534;
}
#animation #mountains .cls-9 {
  fill: #ea4c7d;
}
#animation #mountains .cls-10 {
  fill: #ec497b;
}
#animation #mountains .cls-11 {
  fill: #ec4a7b;
}
#animation #mountains #Visage {
  display: none;
}
#animation #mountains #fissures {
  opacity: 0;
}
#animation #mountains #Lac #Gel {
  opacity: 0;
}
#animation #mountains #Neige_cretes path {
  opacity: 0;
}


